@import url(https://fonts.googleapis.com/css2?family=Road+Rage&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: "Road Rage", cursive;
  text-align: center;
  padding: 5rem;
  background-image: linear-gradient(
    90deg,
    hsla(33, 100%, 53%, 1) 0%,
    hsla(58, 100%, 68%, 1) 100%
  );
}

button {
  font-family: "Road Rage", cursive;
  padding: 0.8rem 2rem;
  border: none;
  background-color: aliceblue;
  margin: 1rem;
}

.circles {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.circle {
  height: 200px;
  width: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(76, 170, 115);
  border-radius: 50%;
  margin: 1rem;
  color: #fff;
  cursor: pointer;
  box-shadow: 0 0 10px rgb(72, 72, 72);
}
.circle p {
  font-size: 2.5rem;
}
.active {
  box-shadow: 0 0 5px cyan;
  border: 25px solid fuchsia;
}
#overlay {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  cursor: pointer;
}

.App {
  text-align: center;
  font-family: "Road Rage", cursive;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
